import React from "react";
import Cart from './Cart';
import Time from './Time';

class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			val:0,
			clas : 'cart',
			clas1 : 'cart',
		
		}
		this.Clic = this.Clic.bind(this);
		
	}
	Clic() {
		
		if(this.state.clas==='not cart'||this.state.clas1==='not cart') return
		if(Math.random()>0.5){
			this.setState({ clas1:'cart' });
			this.setState({ clas:'cart' });
			this.setState({ clas:'done cart' });
			this.setState({val: this.state.val + 1});
			setTimeout(()=>this.setState({ clas:'cart' }),1000);
		} else {
			this.setState({ clas:'not cart' });
			

		}
		
		
	
	}
	Clic1() {
		
		if(this.state.clas==='not cart'||this.state.clas1==='not cart') return
		if(Math.random()>0.5){
			this.setState({ clas:'cart' });
			this.setState({ clas1:'cart' });
			this.setState({ clas1:'done cart' });
			this.setState({val: this.state.val + 1});
			setTimeout(()=>this.setState({ clas1:'cart' }),1000);
		} else {
			this.setState({ clas1:'not cart' });
			
		}
		
	
	}
	Reset(){
		this.setState({ clas:'cart' });
			this.setState({ clas1:'cart' });
			this.setState({val: 0});
	}
	render() { 
		
		let lost;
		
		let button;
		
		if(this.state.clas==='not cart'||this.state.clas1==='not cart'||this.state.val===3) {
lost=<div className='lost'>{this.state.val!==3?'Вы проиграли!':'Вы победили!!'}</div>
			button=<button className='return' onClick={()=>this.Reset()}>Начать заново</button>
		}
// 	
		return(
		<div className='wrap'>
			<Time/>
			<div className='flex'>
			<div className='num'>Сыграем до 3х</div>
			<div className='num'>Угаданно: {this.state.val}</div></div>
			
			{lost}
			<div className='pole' >
				
				<Cart clas={this.state.clas} cart='1' onClick={() => this.Clic()}/>
				<Cart clas={this.state.clas1} cart='2' onClick={() => this.Clic1()}/>
			

</div>
      {button }
		</div> 
	
	);
}
}

export default App;
